import React from 'react'
import Link from 'gatsby-link'




const IndexPage = () => (
<div className="backgroundImage">
  <div>
    <h1>Hi People</h1>
    <p>I am Harish Barge.</p>
    <p>Now go build something great.</p>

  <div>
      <Link className="a-button" to="/page-2/">About Me</Link>
  </div>

  <div className="m-top-10">
      <Link className="a-button" to="/page-3/">Contact</Link>
  </div>
  </div>

  </div>
)

export default IndexPage



